    content: '\2807';
  }
  90% {
    content: '\280F';
  }
}

.spinner {
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  min-width: 100vw;
  position: fixed;
  left: center;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 5;
}

.spinner:after {
  -webkit-animation: changeContent 0.8s linear infinite;
          animation: changeContent 0.8s linear infinite;
  display: block;
  content: '\280B';
  font-size: 80px;
}

